import { PropTypes } from 'prop-types';
import './ModalDialog.css';
import { useState, useEffect } from 'react';

export const ModalDialog = ({ display, title, message }) => {
    const [displayModal, setDisplayModal] = useState(display);
    
    useEffect(() => {
        setDisplayModal(display);
    }, [display]);

    return (
        <div className='modal' style={{ 'display': !displayModal ? 'none' : '' }}>
            <div className='modal-content'>
                <span className='close' onClick={ () => setDisplayModal(false) }>&times;</span>
                <h2>{ title }</h2>
                <p>{ message }</p>

                <button onClick={ () => setDisplayModal(false) }>Aceptar</button>
                <button onClick={ () => setDisplayModal(false) }>Cancelar</button>
            </div>
        </div>
    );
}

ModalDialog.propTypes = {
    display: PropTypes.bool,
    title: PropTypes.string,
    message: PropTypes.string,
    // primary: PropTypes.bool,
};

ModalDialog.defaultProps = {
    display: false,
    title: 'Modal title',
    message: 'This is the content of the modal.',
    // primary: true,
}